[aria-sort] button,
[aria-sort] button:hover {
  position: relative;
  margin: 0;
  padding: 0 10px 0 0;
  border-width: 0;
  color: #0b0c0c;
  background-color: transparent;
  box-shadow: 0 0 0 0;
  font-family: inherit;
  font-size: inherit;
  font-size: 1em;
  font-weight: inherit;
  text-align: inherit;
  text-decoration: underline;
  cursor: pointer;
}

[aria-sort] button:focus {
  outline: none;
  color: #0b0c0c;
  background-color: #ffdd00;
  box-shadow: 0 -2px #ffdd00, 0 4px #0b0c0c;
}

[aria-sort]:first-child button {
  right: auto;
}

[aria-sort] button:before {
  content: " \25bc";
  position: absolute;
  top: 9px;
  right: -1px;
  font-size: .5em;
}

[aria-sort] button:after {
  content: " \25b2";
  position: absolute;
  top: 1px;
  right: -1px;
  font-size: .5em;
}

[aria-sort="ascending"] button:before,
[aria-sort="descending"] button:before {
  content: none;
}

[aria-sort="ascending"] button:after {
  content: " \25b2";
  position: absolute;
  top: 2px;
  right: -5px;
  font-size: .8em;
}

[aria-sort="descending"] button:after {
  content: " \25bc";
  position: absolute;
  top: 2px;
  right: -5px;
  font-size: .8em;
}

