[aria-sort] button,
[aria-sort] button:hover {
  // sass-lint:disable-block no-duplicate-properties
  position: relative;
  margin: 0;
  padding: 0 10px 0 0;
  border-width: 0;
  color:  $govuk-text-colour;
  background-color: transparent;
  //-webkit-box-shadow: 0 0 0 0;
  //-moz-box-shadow: 0 0 0 0;
  box-shadow: 0 0 0 0;
  font-family: inherit;
  font-size: inherit;
  font-size: 1em;
  font-weight: inherit;
  text-align: inherit;
  text-decoration: underline;
  cursor: pointer;
}

[aria-sort] button:focus {
  outline: none;
  color: $govuk-focus-text-colour;
  background-color: $govuk-focus-colour;
  box-shadow: 0 -2px $govuk-focus-colour, 0 4px $govuk-focus-text-colour;
}

[aria-sort]:first-child button {
  right: auto;
}

[aria-sort] button:before {
  content: " \25bc";
  position: absolute;
  top: 9px;
  right: -1px;
  font-size: .5em;
}

[aria-sort] button:after {
  content: " \25b2";
  position: absolute;
  top: 1px;
  right: -1px;
  font-size: .5em;
}

[aria-sort="ascending"] button:before,
[aria-sort="descending"] button:before {
  content: none;
}

[aria-sort="ascending"] button:after {
  content: " \25b2";
  position: absolute;
  top: 2px;
  right: -5px;
  font-size: .8em;
}

[aria-sort="descending"] button:after {
  content: " \25bc";
  position: absolute;
  top: 2px;
  right: -5px;
  font-size: .8em;
}

.sortable-table__status {
  @include govuk-font($size: 16);
}
